import React, { useState } from 'react';
import './files.css';
// import './pages/files/files'; // Create a separate CSS file for styling
// import Files from "./pages/files/files";

// "./pages/docs/Docs";
interface UploadedFile {
    name: string;
    size: string;
    date: string;
}

interface FileGroup {
    name: string;
    files: UploadedFile[];
}

const Files: React.FC = () => {
    const [uploadedFiles, setUploadedFiles] = useState<UploadedFile[]>([]); // Stores uploaded files
    const [groups, setGroups] = useState<FileGroup[]>([]); // Stores file groups
    const [activeTab, setActiveTab] = useState<string>('File Collection'); // Track active tab

    const handleFileUpload = (event: React.ChangeEvent<HTMLInputElement>) => {
        const files = Array.from(event.target.files || []); // Convert FileList to Array
        const newFiles = files.map((file) => ({
            name: file.name,
            size: `${(file.size / 1024).toFixed(2)} KB`,
            date: new Date().toLocaleString(),
        }));
        setUploadedFiles((prev) => [...prev, ...newFiles]);
    };

    const createGroup = () => {
        if (uploadedFiles.length > 0) {
            setGroups((prev) => [
                ...prev,
                { name: `Group ${prev.length + 1}`, files: uploadedFiles },
            ]);
            setUploadedFiles([]); // Clear uploaded files after grouping
        }
    };

    return (
        <div className="files-section">
            <div className="tabs">
                <button
                    className={activeTab === 'File Collection' ? 'active' : ''}
                    onClick={() => setActiveTab('File Collection')}
                >
                    File Collection
                </button>
                <button
                    className={activeTab === 'GraphRAG Collection' ? 'active' : ''}
                    onClick={() => setActiveTab('GraphRAG Collection')}
                >
                    GraphRAG Collection
                </button>
            </div>

            {activeTab === 'File Collection' && (
                <div className="file-collection">
                    <div className="upload-section">
                        <label htmlFor="file-upload" className="upload-box">
                        {/* <label htmlFor="UploadFile" className="upload-box"> */}
                            <div>
                                <span></span>
                                <p>Drop File Here - or - Click to Upload</p>
                            </div>
                            <input
                                id="file-upload"
                                type="file"
                                multiple
                                onChange={handleFileUpload}
                                style={{ display: 'none' }}
                            />
                        </label>
                        <small>
                            Supported file types: .png, .jpeg, .pdf, etc. (Max size: 1GB)
                        </small>
                    </div>
                    <div className="file-list">
                        <h3>Files</h3>
                        {uploadedFiles.length > 0 ? (
                            <ul>
                                {uploadedFiles.map((file, index) => (
                                    <li key={index}>
                                        <span>{file.name}</span>
                                        <span>{file.size}</span>
                                        <span>{file.date}</span>
                                    </li>
                                ))}
                            </ul>
                        ) : (
                            <p>No files uploaded yet.</p>
                            // <p style="color: #ffffff;">No files uploaded yet.</p>
                            // <p style="color: #ffffff; background-color: #333333;">No files uploaded yet.</p>

                        )}
                    </div>
                    <div className="group-section">
                        <h3>Groups</h3>
                        <button onClick={createGroup}>Create Group</button>
                        {groups.length > 0 ? (
                            <ul>
                                {groups.map((group, index) => (
                                    <li key={index}>
                                        <strong>{group.name}</strong>
                                        <ul>
                                            {group.files.map((file, fileIndex) => (
                                                <li key={fileIndex}>{file.name}</li>
                                            ))}
                                        </ul>
                                    </li>
                                ))}
                            </ul>
                        ) : (
                            <p>No groups created yet.</p>
                        )}
                    </div>
                </div>
            )}

            {activeTab === 'GraphRAG Collection' && (
                <div className="graphrag-collection">
                    <h3>GraphRAG Collection</h3>
                    <p>Manage GraphRAG files here.</p>
                </div>
            )}
        </div>
    );
}
export default Files;
