import React, { useState } from "react";
import "./help.css";

const Help: React.FC = () => {
    const [activeSection, setActiveSection] = useState<"about" | "userGuide">("about");

    return (
        <div className="help-container">
            <div className="dropdown-container">
                {/* About Section */}
                <div
                    className={`dropdown-item ${activeSection === "about" ? "active" : ""}`}
                    onClick={() => setActiveSection("about")}
                >
                    <h3>About</h3>
                    {activeSection === "about" && (
                        <div className="dropdown-content">
                            <p>
                                <strong>Version:</strong> Local
                            </p>
                            <h3>Braein Energy AI Data Explorer</h3>
                            <p>
                                Welcome to the Braein Energy AI Data Explorer! This platform is designed to help users
                                manage files, explore resources, and interact with AI-powered tools to streamline
                                workflows and enhance productivity. Navigate through the tabs to explore the various
                                features we offer.
                            </p>
                        </div>
                    )}
                </div>

                {/* User Guide Section */}
                <div
                    className={`dropdown-item ${activeSection === "userGuide" ? "active" : ""}`}
                    onClick={() => setActiveSection("userGuide")}
                >
                    <h3>User Guide</h3>
                    {activeSection === "userGuide" && (
                        <div className="dropdown-content">
                            <h4>1. Add Your AI Models</h4>
                            <p>
                                Navigate to the <strong>Resources</strong> tab to manage LLMs and embeddings:
                            </p>
                            <ul>
                                <li>
                                    <strong>View:</strong> Explore available LLMs and embeddings in their respective
                                    sections.
                                </li>
                                <li>
                                    <strong>Add:</strong> Add new LLMs or embeddings by providing required details and
                                    configuration.
                                </li>
                            </ul>

                            <h4>2. Manage Files</h4>
                            <p>
                                Use the <strong>Files</strong> tab to upload, index, and group files. The platform
                                supports various formats, including PDFs, images, and more. Use the grouping feature to
                                manage multiple files.
                            </p>

                            <h4>3. Query and Explore Data</h4>
                            <p>
                                Leverage the <strong>Chat</strong> tab to ask questions about uploaded files or indexed
                                data. The AI assistant provides contextual answers and highlights relevant sections in
                                the uploaded documents.
                            </p>

                            <h4>4. Configuration</h4>
                            <p>
                                Visit the <strong>Settings</strong> tab to configure platform preferences, toggle
                                between light/dark modes, and manage user profiles.
                            </p>

                            <h4>5. Integration</h4>
                            <p>
                                The platform integrates with various AI and data management tools, allowing seamless
                                interaction with large language models, embedding systems, and custom data sets.
                            </p>

                            <h4>6. Advanced Features</h4>
                            <p>
                                <strong>File Groups:</strong> Use the grouping feature to manage related documents
                                together.<br />
                                <strong>Interactive Highlights:</strong> While querying files, the assistant
                                automatically highlights relevant sections.
                            </p>

                            <p>Explore more features by navigating through the tabs!</p>
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
};

export default Help;
