import { useRef, useState, useEffect, useContext, useMemo } from "react";
import { Dropdown, IDropdownOption, TextField } from "@fluentui/react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faAngleDoubleDown, faAngleDoubleUp } from "@fortawesome/free-solid-svg-icons";
import styles from "./LeftSidebar.module.css";
import { faAngleDoubleRight, faAngleDoubleLeft } from "@fortawesome/free-solid-svg-icons";
import FileUpload from "../../components/FileUpload/FileUpload";
import { useLogin, getToken } from "../../authConfig";
import { useMsal } from "@azure/msal-react";
import { HistoryButton } from "../../components/HistoryButton";
import { SimpleAPIResponse, uploadFileApi, deleteUploadedFileApi, listUploadedFilesApi } from "../../api";
import { UploadFile } from "../../components/UploadFile";
import chats from "../chat/Chat";
import { LoginContext } from "../../loginContext";
import { ClearChatButton } from "../../components/ClearChatButton";
import { Button } from "@fluentui/react-components";
import { Delete24Regular } from "@fluentui/react-icons";
import { Text } from "@fluentui/react";
import { useTranslation } from "react-i18next";
import { Panel, PanelType } from "@fluentui/react";
import { HistoryData, HistoryItem } from "../../components/HistoryItem";
import { useHistoryManager, HistoryMetaData } from "../../components/HistoryProviders";
const HISTORY_COUNT_PER_LOAD = 20;
const LeftSidebar = ({ provider, isOpen, notify, onClose, onChatSelected, historyManager }) => {
    const [conversationOptions, setConversationOptions] = useState<IDropdownOption[]>([]);
    const { loggedIn } = useContext(LoginContext);

    // const [selectedConversation, setSelectedConversation] = useState<string | undefined>(undefined);
    const [isSidebarEnabled, setIsSidebarEnabled] = useState<boolean>(true);
    // const [isHistoryPanelOpen, setIsHistoryPanelOpen] = useState(false);
    // Section toggle states
    const [isFileCollectionOpen, setFileCollectionOpen] = useState<boolean>(true);
    const [isGraphRAGCollectionOpen, setGraphRAGCollectionOpen] = useState<boolean>(true);
    const [isQuickUploadOpen, setQuickUploadOpen] = useState<boolean>(true);
    const [isFeedbackOpen, setFeedbackOpen] = useState<boolean>(true);
    const [uploadedFile, setUploadedFile] = useState<SimpleAPIResponse>();

    const [isUploading, setIsUploading] = useState<boolean>(false);
    const [uploadedFiles, setUploadedFiles] = useState<string[]>([]); // List of uploaded files
    const client = useMsal().instance;
    const [isLoading, setIsLoading] = useState(true);
    const [deletionStatus, setDeletionStatus] = useState({});
    const [history, setHistory] = useState<HistoryMetaData[]>([]);
    const [hasMoreHistory, setHasMoreHistory] = useState(false);

    const [searchQuery, setSearchQuery] = useState<string>("");

    const [isDropdownOpen, setIsDropdownOpen] = useState(false);

    const groupedHistory = useMemo(() => groupHistory(history), [history]);
    function groupHistory(history: HistoryData[]) {
        const today = new Date();
        today.setHours(0, 0, 0, 0);
        const yesterday = new Date(today);
        yesterday.setDate(yesterday.getDate() - 1);
        const lastWeek = new Date(today);
        lastWeek.setDate(lastWeek.getDate() - 7);
        const lastMonth = new Date(today);
        lastMonth.setDate(lastMonth.getDate() - 30);

        return history.reduce(
            (groups, item) => {
                const itemDate = new Date(item.timestamp);
                let group;

                if (itemDate >= today) {
                    group = "history.today";
                } else if (itemDate >= yesterday) {
                    group = "history.yesterday";
                } else if (itemDate >= lastWeek) {
                    group = "history.last7days";
                } else if (itemDate >= lastMonth) {
                    group = "history.last30days";
                } else {
                    group = itemDate.toLocaleDateString(undefined, { year: "numeric", month: "long" });
                }

                if (!groups[group]) {
                    groups[group] = [];
                }
                groups[group].push(item);
                return groups;
            },
            {} as Record<string, HistoryData[]>
        );
    }
    // const [showChatHistoryBrowser, setShowChatHistoryBrowser] = useState<boolean>(false);
    // const historyManager = useHistoryManager(provider);
    const handleSelect = async (id: string) => {
        const item = await historyManager.getItem(id);
        if (item) {
            onChatSelected(item);
        }
    };
    useEffect(() => {
        setHistory([]);
        historyManager.resetContinuationToken();
        setHasMoreHistory(true);
    }, []);


    const handleUploadFile = async (le: File) => {

        setIsUploading(true); // Start the loading state
        const file: File = le
        console.log(file)
        const formData = new FormData();
        formData.append("file", file);

        try {
            const idToken = await getToken(client);
            if (!idToken) {
                throw new Error("No authentication token available");
            }
            const response: SimpleAPIResponse = await uploadFileApi(formData, idToken);
            setUploadedFile(response);
            setIsUploading(false);
            getfiles();
            // setUploadedFileError(undefined);
            // listUploadedFiles(idToken);
        } catch (error) {
            console.error(error);
            setIsUploading(false);
            // setUploadedFileError(t("upload.uploadedFileError"));
        }
    };

    const listUploadedFiles = async idToken => {
        listUploadedFilesApi(idToken).then(files => {
            setIsLoading(false);
            setDeletionStatus({});
            setUploadedFiles(files);
        });
    };

    const handleRemoveFile = async (filename: string) => {
        setDeletionStatus({ ...deletionStatus, [filename]: "pending" });

        try {
            const idToken = await getToken(client);
            if (!idToken) {
                throw new Error("No authentication token available");
            }

            await deleteUploadedFileApi(filename, idToken);
            setDeletionStatus({ ...deletionStatus, [filename]: "success" });
            listUploadedFiles(idToken);
        } catch (error) {
            setDeletionStatus({ ...deletionStatus, [filename]: "error" });
            console.error(error);
        }
    };

    const handleDelete = async (id: string) => {
        await historyManager.deleteItem(id);
        setHistory(prevHistory => prevHistory.filter(item => item.id !== id));
    };

    const [searchTerm, setSearchTerm] = useState("");

    const filteredConversations = conversationOptions.filter((conversation) =>
        conversation.text.toLowerCase().includes(searchTerm.toLowerCase())
    );

    const handleDeleteConversation = async (conversationId) => {
        await historyManager.deleteItem(conversationId);
        setConversationOptions((prevOptions) =>
            prevOptions.filter((option) => option.key !== conversationId)
        );
    };



    const getfiles = async () => {
        try {
            const idToken = await getToken(client);
            if (!idToken) {
                throw new Error("No authentication token available");
            }
            listUploadedFiles(idToken);
        } catch (error) {
            console.error(error);
            setIsLoading(false);
        }
    };

    useEffect(() => {
        if (!isOpen) return;
        if (notify) {
            setHistory([]);
            historyManager.resetContinuationToken();
            setHasMoreHistory(true);
        }
    }, [isOpen, notify]);

    const loadMoreHistory = async () => {
        setIsLoading(() => true);
        const items = await historyManager.getNextItems(HISTORY_COUNT_PER_LOAD);
        if (items.length === 0) {
            setHasMoreHistory(false);
        }
        setHistory(prevHistory => [...prevHistory, ...items]);
        setIsLoading(() => false);
    };
    useEffect(() => {

        getfiles();
    }, []);
    const { t } = useTranslation();

    const InfiniteLoadingButton = ({ func }: { func: () => void }) => {
        const buttonRef = useRef(null);

        useEffect(() => {
            const observer = new IntersectionObserver(
                entries => {
                    entries.forEach(entry => {
                        if (entry.isIntersecting) {
                            if (buttonRef.current) {
                                func();
                            }
                        }
                    });
                },
                {
                    root: null,
                    threshold: 0
                }
            );

            if (buttonRef.current) {
                observer.observe(buttonRef.current);
            }

            return () => {
                if (buttonRef.current) {
                    observer.unobserve(buttonRef.current);
                }
            };
        }, []);

        return <button ref={buttonRef} onClick={func} />;
    };

    useEffect(() => {
        setConversationOptions(Object.entries(groupedHistory).flatMap(([group, items]) =>
            items.map((item) => ({
                key: item.id,
                text: item.title,
            }))
        ))

        // You can do something with conversationOptions here, e.g., update state.
        // Example: setConversationOptions(conversationOptions);

    }, [groupedHistory]); // Make sure to include the dependency in the array



    return (
        <div className={`${styles.leftSidebar} ${styles.visible}`}>
            {isSidebarEnabled && (
                <>
                    {/* <h3>Conversations</h3>
                     // <HistoryButton className={styles.commandButton} onClick={() => setIsHistoryPanelOpen((e) => !e)} />
                    <Dropdown
                        placeholder="Select a conversation"
                        options={conversationOptions}
                        onChange={(_, option) => {
                            // Safely handle undefined option and ensure key is a string
                            if (option?.key) {
                                handleSelect(String(option.key)); // Convert key to string if it's not already
                            }
                        }}
                        onClick={() => loadMoreHistory()}
                    />*/}
                    {/* <h3>Conversations</h3>
                    <div className={`${styles.dropdown} ${styles.dropdownWithSearch}`}>
                        <button
                            onClick={() => setIsDropdownOpen(!isDropdownOpen)}
                            className={styles.dropbtn}
                        >
                            Select a conversation
                        </button>
                        {isDropdownOpen && (
                            <div className={styles.dropdownContent}>
                                <input
                                    type="text"
                                    placeholder="Search conversations..."
                                    value={searchTerm}
                                    onChange={(e) => setSearchTerm(e.target.value)}
                                    className={styles.searchInput}
                                    onClick={() => loadMoreHistory()}
                                />
                                {filteredConversations.length > 0 ? (
                                    filteredConversations.map((conversation) => (
                                        <div
                                            key={conversation.key}
                                            className={styles.conversationOption}

                                            onClick={() => {
                                                handleSelect(String(conversation.key));
                                                setIsDropdownOpen(false); // Close dropdown on selection
                                            }}
                                        >
                                            <span>{conversation.text}</span>
                                            <button
                                                onClick={(e) => {
                                                    e.stopPropagation(); // Prevent dropdown closing
                                                    handleDeleteConversation(conversation.key);
                                                }}
                                                className={styles.deleteButton}
                                            >
                                                Delete
                                            </button>
                                        </div>
                                    ))
                                ) : (
                                    <span className={styles.noOptions}>No conversations found</span>
                                )}
                            </div>
                        )}
                    </div> */}




                    <h3>Conversations</h3>
                    <Dropdown
                        className={styles.dropdownWithSearch}
                        placeholder="Select a conversation"
                        onClick={() => loadMoreHistory()}
                        // onRenderTitle={() => (
                        //     <input
                        //         type="text"
                        //         placeholder="Search conversations..."
                        //         value={searchTerm}
                        //         onChange={(e) => setSearchTerm(e.target.value)}
                        //         className={styles.searchInput}
                        //     />
                        // )}
                        options={filteredConversations.map((conversation) => ({
                            key: conversation.key,
                            text: conversation.text, // Keep text simple for the Dropdown API
                        }))}
                        onRenderOption={(option) => (
                            <div className={styles.conversationOption}>
                                <span>{option?.text}</span>
                                <button
                                    onClick={(e) => {
                                        e.stopPropagation(); // Prevent dropdown selection on delete click
                                        handleDeleteConversation(option?.key);
                                    }}
                                    className={styles.deleteButton}
                                >
                                    <Delete24Regular />
                                </button>
                            </div>
                        )}
                        onChange={(_, option) => {
                            if (option?.key) {
                                handleSelect(String(option.key));
                            }
                        }}
                    />





                    {/* <Dropdown
                className={styles.dropdownWithSearch}
                placeholder="Select a conversation"
                onClick={() => loadMoreHistory()}
                onRenderTitle={() => (
                    <input
                        type="text"
                        placeholder="Search conversations..."
                        value={searchTerm}
                        onChange={(e) => setSearchTerm(e.target.value)}
                        className={styles.searchInput}
                    />
                )}
                options={filteredConversations.map((conversation) => ({
                    key: conversation.key,
                    text: (
                        <div className={styles.conversationOption}>
                            <span>{conversation.text}</span>
                            <button
                                onClick={(e) => {
                                    e.stopPropagation(); // Prevent dropdown selection on delete click
                                    handleDeleteConversation(conversation.key);
                                }}
                                className={styles.deleteButton}
                            >
                                Delete
                            </button>
                        </div>
                    ),
                }))}
                onChange={(_, option) => {
                    if (option?.key) {
                        handleSelect(String(option.key));
                    }
                }}
            /> */}





                    {/* <div>
                        <div>
                            {Object.entries(groupedHistory).map(([group, items]) => (
                                <div key={group} className={styles.group}>
                                    <p className={styles.groupLabel}>{t(group)}</p>
                                    {items.map(item => (
                                        <HistoryItem key={item.id} item={item} onSelect={handleSelect} onDelete={handleDelete} />
                                    ))}
                                </div>
                            ))}
                            {history.length === 0 && <p>{t("history.noHistory")}</p>}
                            {hasMoreHistory && !isLoading && <InfiniteLoadingButton func={loadMoreHistory} />}
                        </div>
                    </div> */}

                    {/* File Collection Section */}
                    <div>
                        <h3
                            className={styles.sectionHeader}
                            onClick={() => setFileCollectionOpen(!isFileCollectionOpen)}
                        >
                            File Collection
                            <FontAwesomeIcon
                                icon={isFileCollectionOpen ? faAngleDoubleUp : faAngleDoubleDown}
                                className={styles.sectionToggleIcon}
                            />
                        </h3>
                        {isFileCollectionOpen && (
                            <div>
                                {/* Search Bar */}
                                <input
                                    type="text"
                                    placeholder="Search files..."
                                    className={styles.searchInput}
                                    value={searchQuery}
                                    onChange={(e) => setSearchQuery(e.target.value)}
                                />
                                {/* File List with Delete Option */}
                                <div
                                    className={`${styles.fileList} ${uploadedFiles.length > 3 ? styles.scrollableList : ""
                                        }`}
                                >
                                    {uploadedFiles.length > 0 ? (
                                        <ul>
                                            {uploadedFiles
                                                .filter((file) =>
                                                    file.toLowerCase().includes(searchQuery.toLowerCase())
                                                )
                                                .map((file, index) => (
                                                    <li key={index}>
                                                        {file}
                                                        <button
                                                            className={styles.deleteButton}
                                                            onClick={() => handleRemoveFile(file)}
                                                        >
                                                            <Delete24Regular />
                                                        </button>
                                                    </li>
                                                ))}
                                        </ul>
                                    ) : (
                                        <p>No files uploaded yet.</p>
                                    )}
                                </div>
                            </div>
                        )}
                    </div>

                    {/* <div>
                                //  <button className={styles.dropdownButton}>Search All</button>
                                // <button className={styles.dropdownButton}>Search in Files</button> 
                                <div className={styles.fileList}>
                                    {uploadedFiles.length > 0 ? (
                                        <ul>
                                            {uploadedFiles.map((file, index) => (
                                                <li key={index}>{file}</li>
                                            ))}
                                        </ul>
                                    ) : (
                                        <p>No files uploaded yet.</p>
                                    )}
                                </div>
                            </div> */}


                    {/* GraphRAG Collection Section */}
                    {/* <div>
                        <h3
                            className={styles.sectionHeader}
                            onClick={() => setGraphRAGCollectionOpen(!isGraphRAGCollectionOpen)}
                        >
                            GraphRAG Collection
                            <FontAwesomeIcon
                                icon={isGraphRAGCollectionOpen ? faAngleDoubleUp : faAngleDoubleDown}
                                className={styles.sectionToggleIcon}
                            />
                        </h3>
                        {isGraphRAGCollectionOpen && (
                            <div>
                                <button className={styles.dropdownButton}>Search Graphs</button>
                            </div>
                        )}
                    </div> */}

                    {/* Quick Upload Section */}
                    <div>
                        <h3
                            className={styles.sectionHeader}
                            onClick={() => setQuickUploadOpen(!isQuickUploadOpen)}
                        >
                            Quick Upload
                            <FontAwesomeIcon
                                icon={isQuickUploadOpen ? faAngleDoubleUp : faAngleDoubleDown}
                                className={styles.sectionToggleIcon}
                            />
                        </h3>
                        {isQuickUploadOpen && (
                            <div className={styles.uploadContainer}>
                                {!isUploading ? <p>Drop File Here</p> : null}
                                {isUploading ? (
                                    <div>
                                        <p>1 file is uploading. Please wait...</p>
                                    </div>
                                ) : loggedIn ? ( // Check if the user is logged in
                                    <FileUpload handleUploadFile={handleUploadFile} />
                                ) : (
                                    <p>Please log in to upload files.</p> // Message for non-logged-in users
                                )}
                            </div>
                            // <div className={styles.uploadContainer}>
                            //     <p>Drop File Here</p>
                            //     {isUploading ? (
                            //         <div>
                            //             <p>1 file is uploading. Please wait...</p>
                            //         </div>
                            //     ) : (
                            //         // <FileUpload onFileUpload={(acceptedFiles) => handleFileUpload(acceptedFiles)} />
                            //         <FileUpload handleUploadFile={handleUploadFile} />
                            //         //  <UploadFile className = {styles.commandButton} disabled= {!loggedIn}/>

                            //     )}
                            // </div>
                        )}
                    </div>

                    {/* Feedback Section */}
                    <div>
                        <h3
                            className={styles.sectionHeader}
                            onClick={() => setFeedbackOpen(!isFeedbackOpen)}
                        >
                            Feedback
                            <FontAwesomeIcon
                                icon={isFeedbackOpen ? faAngleDoubleUp : faAngleDoubleDown}
                                className={styles.sectionToggleIcon}
                            />
                        </h3>
                        {isFeedbackOpen && (
                            <div>
                                <TextField placeholder="Provide feedback here..." multiline />
                                <button className={styles.feedbackButton}>Submit Feedback</button>
                            </div>
                        )}
                    </div>
                </>
            )}
        </div>
    );
};

export default LeftSidebar;