import React, { useState, useEffect, useRef, RefObject } from "react";
import { Outlet, NavLink, Link } from "react-router-dom";
import { useTranslation } from "react-i18next";
import styles from "./Layout.module.css";
import logo from "D:/DataExplorer/newdemo/azure-search-openai-demo/app/frontend/src/assets/braein.png";
import istLogo from "D:/DataExplorer/newdemo/azure-search-openai-demo/app/frontend/src/assets/ist_logo_footer.svg";
import { useLogin } from "../../authConfig";
import { LoginButton } from "../../components/LoginButton";
import { IconButton } from "@fluentui/react";
import LeftSidebar from "../leftsidebar/LeftSidebar"
import Help from "../Help/help"
const Layout = () => {
    const [isSidebarVisible, setIsSidebarVisible] = useState(true);
    const { t } = useTranslation();
    const [menuOpen, setMenuOpen] = useState(false);
    const menuRef: RefObject<HTMLDivElement> = useRef(null);

    const toggleMenu = () => {
        setMenuOpen(!menuOpen);
    };

    const handleClickOutside = (event: MouseEvent) => {
        if (menuRef.current && !menuRef.current.contains(event.target as Node)) {
            setMenuOpen(false);
        }
    };

    useEffect(() => {
        if (menuOpen) {
            document.addEventListener("mousedown", handleClickOutside);
        } else {
            document.removeEventListener("mousedown", handleClickOutside);
        }
        return () => {
            document.removeEventListener("mousedown", handleClickOutside);
        };
    }, [menuOpen]);

    return (
        <div className={styles.layout}>
            <header className={styles.header} role={"banner"}>
                <div className={styles.headerContainer} ref={menuRef}>
                    <Link to="/" className={styles.headerTitleContainer}>
                        <h3 className={styles.headerTitle}>{t("headerTitle")}</h3>
                    </Link>
                    <div className={styles.headerContainer}>
                        <Link
                            to="/"
                            className={styles.headerTitleContainer}
                            style={{
                                width: "100px",
                                height: "auto",
                                marginLeft: "10px"
                            }}
                        >
                            <img
                                src={logo}
                                className="navbar-logo"
                                style={{
                                    background: "none",
                                    width: "100px",
                                    height: "auto"
                                }}
                            />
                        </Link>
                        <p
                            style={{
                                color: "white",
                                fontSize: "20px",
                                fontWeight: "bold",
                                margin: "0 10px"
                            }}
                        >
                            |
                        </p>
                        <Link
                            to="/"
                            className={styles.headerTitleContainer}
                            style={{
                                width: "100px",
                                height: "auto"
                            }}
                        >
                            <img
                                src={istLogo}
                                className="navbar-logo"
                                style={{
                                    background: "none",
                                    width: "100px",
                                    height: "auto"
                                }}
                            />
                        </Link>
                    </div>
                    <div>
                        <nav>
                            <ul className={`${styles.headerNavList} ${menuOpen ? styles.show : ""}`}>
                                <li>
                                    <NavLink
                                        to="/"
                                        className={({ isActive }) => (isActive ? styles.headerNavPageLinkActive : styles.headerNavPageLink)}
                                        onClick={() => setMenuOpen(false)}
                                    >
                                        {t("chat")}
                                    </NavLink>
                                </li>
                                {/* <li>
                                    <NavLink
                                        to="/files"
                                        className={({ isActive }) =>
                                            isActive ? styles.headerNavPageLinkActive : styles.headerNavPageLink
                                        }
                                        onClick={() => setMenuOpen(false)}
                                    >
                                        {t("files")}
                                    </NavLink>
                                </li> */}
                                <li>
                                    <NavLink
                                        to="/docs"
                                        className={({ isActive }) => (isActive ? styles.headerNavPageLinkActive : styles.headerNavPageLink)}
                                        onClick={() => setMenuOpen(false)}
                                    >
                                        Files
                                    </NavLink>
                                </li>
                                <li>
                                    <NavLink
                                        to="/Resources"
                                        className={({ isActive }) => (isActive ? styles.headerNavPageLinkActive : styles.headerNavPageLink)}
                                        onClick={() => setMenuOpen(false)}
                                    >
                                        Resources
                                    </NavLink>
                                </li>
                                <li>
                                    <NavLink
                                        to="/Settings"
                                        className={({ isActive }) => (isActive ? styles.headerNavPageLinkActive : styles.headerNavPageLink)}
                                        onClick={() => setMenuOpen(false)}
                                    >
                                        Settings
                                    </NavLink>
                                </li>
                                <li>
                                    <NavLink
                                        to="/help"
                                        className={({ isActive }) => (isActive ? styles.headerNavPageLinkActive : styles.headerNavPageLink)}
                                        onClick={() => setMenuOpen(false)}
                                    >
                                        Help
                                    </NavLink>
                                </li>
                                {/* <li>
                                    <NavLink
                                        to="/qa"
                                        className={({ isActive }) => (isActive ? styles.headerNavPageLinkActive : styles.headerNavPageLink)}
                                        onClick={() => setMenuOpen(false)}
                                    >
                                        {t("qa")}
                                    </NavLink>
                                </li> */}
                            </ul>
                        </nav>
                        <div className={styles.loginMenuContainer}>
                            {useLogin && <LoginButton />}
                            <IconButton
                                iconProps={{ iconName: "GlobalNavButton" }}
                                className={styles.menuToggle}
                                onClick={toggleMenu}
                                ariaLabel={t("labels.toggleMenu")}
                            />
                        </div>
                    </div>
                </div>
            </header>
            <Outlet />
            {/* <LeftSidebar isVisible={isSidebarVisible} /> */}
        </div>
    );
};

export default Layout;
